import React, { useContext } from 'react';
import { HeaderContext } from '../../../../../../contexts/HeaderContext';
import Drawer from '../Preview/components/Drawer/Drawer';
import Preview from '../Preview/Preview';
import MainCanvas from "./MainCanvas"
import CardHeader from './tailwindComponents/CardHeader/CardHeader';

const Card: React.FC = (): JSX.Element => {
    const [open, setOpen] = useContext(HeaderContext)


    return (
        <div className="h-full">
            <div>
                <CardHeader />
            </div>
            <div>

                <MainCanvas />
            </div>
            <div className="z-50" >
                <Drawer isOpen={open} setIsOpen={setOpen}></Drawer>
            </div >
            <div className="w-6 bg-radicalRed absolute h-screen right-0 top-14">
                <button className="flex items-center h-full"><svg width="13" height="22" viewBox="0 0 13 22" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M11 20L2 11L11 2" stroke="#374151" stroke-width="4" stroke-linecap="round" stroke-linejoin="round" />
                </svg>
                </button>
            </div>
        </div>
    )
};
export default Card;
